<!-- 处方详情 -->
<template>
	<view class="fff">
		<view class="line"></view>
		<view class="top-div">
			<view class="message-row">
				<view class="txt-message">收货信息</view>
				<view class="txt-type">快递邮寄</view>
				<image style="width: 34rpx; height: 36rpx;" src="../../static/image/icon_qh.png" mode="aspectFill"></image>
			</view>
			<view class="padding-line"></view>
			<view class="user-message-row">
				<image style="width: 26rpx; height: 36rpx;" src="../../static/image/icon_address.png" mode="aspectFill"></image>
				<view class="user-message-txt">姓名 手机号</view>
			</view>
			<view class="user-addres">浙江省杭州市西湖区</view>
		</view>

		<view class="ten-line"></view>

		<view class="top-div">
			<view class="txt-message">处方信息</view>
			<view class="padding-line"></view>
			<view class="list-row">
				<view class="list-before">处方号</view>
				<view class="list-after">0110</view>
			</view>
			<view class="list-row">
				<view class="list-before">医师</view>
				<view class="list-after">王医师</view>
			</view>
			<view class="list-row">
				<view class="list-before">处方名称</view>
				<view class="list-after">感冒</view>
			</view>
			<view class="list-row">
				<view class="list-before">药品类型</view>
				<view class="list-after">配方颗粒</view>
			</view>
			<view class="list-row">
				<view class="list-before">处方信息</view>
				<view class="list-after">保密处方</view>
			</view>
			<view class="list-row">
				<view class="list-before">处方时间</view>
				<view class="list-after">2020/20</view>
			</view>
			<view class="dash-line"></view>
			<view class="list-row">
				<view class="list-before">药品</view>
				<view class="list-after">52</view>
			</view>
			<view class="list-row">
				<view class="list-before">加工费</view>
				<view class="list-after">10</view>
			</view>
			<view class="list-row">
				<view class="list-before">快递费</view>
				<view class="list-after">10</view>
			</view>
		</view>

		<view class="ten-line"></view>

		<view class="top-div">
			<view class="txt-message">支付方式选择</view>
			<view class="padding-line"></view>
			<view class=" uni-list">
				<radio-group @change="radioChange">
					<label class="uni-list-cell" v-for="(item, index) in items" :key="item.value">
						<image style="width: 60rpx; height: 60rpx;" :src="item.img" mode="aspectFill"></image>
						<view style="flex-grow: 1; margin-left: 18rpx;">{{item.name}}</view>
						<view>
							<radio color="#10BE62" style="transform:scale(0.7)" :value="item.value" :checked="index === current" />
						</view>
					</label>
				</radio-group>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				items: [{
						value: 0,
						img: '../../static/image/icon_wx.png',
						name: '微信'
					},
					{
						value: 1,
						img: '../../static/image/icon_zfb.png',
						name: '支付宝',
					}
				],
				current: 0
			}
		},
		methods: {

			radioChange: function(evt) {
				for (let i = 0; i < this.items.length; i++) {
					if (this.items[i].value === evt.target.value) {
						this.current = i;
						console.log("当前选中下标" + this.current);
						break;
					}
				}
			},
		}
	}
</script>

<style>
	@import url("prescription-detail.css");
</style>
